.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 1s linear;
    }

    .App-logo-ud {
        animation: App-logo-up-down infinite 800ms linear;
    }
    .App-logo-ud-1 {
        animation: App-logo-up-down-1 infinite 800ms linear;
    }.App-logo-ud-2 {
        animation: App-logo-up-down-2 infinite 800ms linear;
    }.App-logo-ud-3 {
        animation: App-logo-up-down-3 infinite 800ms linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@keyframes App-logo-up-down {
    0% {
        transform: translateX(0%);
    }
    25% {
        transform: translateX(10rem);
    }
    75% {
        transform: translateX(-10rem);
    }
    100% {
        transform: translateX(0%);
    }
}

@keyframes App-logo-up-down-1 {
    0% {
        transform: translateX(0%);
    }
    25% {
        transform: translateX(-10rem);
    }
    75% {
        transform: translateX(10rem);
    }
    100% {
        transform: translateX(0%);
    }
}

@keyframes App-logo-up-down-2 {
    0% {
        transform: translateY(0%);
    }
    25% {
        transform: translateY(-7rem);
    }
    75% {
        transform: translateY(7rem);
    }
    100% {
        transform: translateY(0%);
    }
}

@keyframes App-logo-up-down-3 {
    0% {
        transform: translateY(0%);
    }
    25% {
        transform: translateY(7rem);
    }
    75% {
        transform: translateY(-7rem);
    }
    100% {
        transform: translateY(0%);
    }
}
